<template>
  <div class="modal-dialog text-center">
    <div class="col-sm-10 main-section">
      <div class="modal-content">
        <div class="col-12 logo-login mb-5" style="color: #337bbe">
          <!-- <img src="/img/img-lock.png" alt="" /> -->
          <!-- <i class="fas fa-robot"></i> -->
          <h3>Panel Administrativo</h3>
          <h3>Chatbot Luisa</h3>
        </div>
        <form class="col-12">
          <div class="form-group mb-4" id="user-group">
            <i class="fas fa-at"></i>
            <input
              type="email"
              class="form-control"
              placeholder="Email"
              v-model="email"
              autocomplete="off"
            />
          </div>
          <div class="form-group mb-5" id="password-group">
            <i class="fas fa-lock"></i>
            <input
              type="password"
              class="form-control"
              placeholder="Password"
              v-model="password"
            />
          </div>
          <div class="form-group mb-5">
            <button
              class="btn btn-primary btn-lg button-login"
              style="background: #337bbe"
              @click.prevent="signIn()"
            >
              <i class="fas fa-sign-in-alt mr-2"></i>Ingresar
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import config from "../config";
export default {
  data: () => {
    return {
      email: "",
      password: "",
    };
  },
  created() {
    this.$store.commit("SET_LAYOUT", "login-layout");
  },
  mounted() {
    if (this.$store.getters.isLoggedIn) {
      this.$router.replace("/");
    }
  },
  methods: {
    signIn() {
      const isValid = this.email && this.password;
      if (!isValid) {
        this.$toastr.e("Debe llenar los campos.");
        return;
      }
      this.$store
        .dispatch("signIn", { email: this.email, password: this.password })
        .then(() => {
          this.$router.push("/reportes/chatbot");
        })
        .catch((error) => {
          if (error.code == "auth/wrong-password") {
            this.$toastr.e("Usuario no registrado o contraseña incorrecta.");
          } else if (error.code == "auth/invalid-email") {
            this.$toastr.e("Email no válido.");
          } else if (error.code == "auth/user-not-found") {
            this.$toastr.e("Usuario no registrado.");
          } else {
            this.$toastr.e(error);
          }
        });
    },
  },
};
</script>
